/** @jsxImportSource @emotion/react */

import styled from "@emotion/styled";
import { useTheme } from "@emotion/react";

function EditButton({
  icon,
  children,
  hoverColor,
  onClick,
  addlCss,
  alwaysVisibleLabel,
}) {
  const theme = useTheme();

  const EditButtonEl = styled.div`
    min-width: 28px;
    height: 28px;
    margin-left: 10px;
    padding: 0px 5px;
    background-color: #ededed;
    border-radius: 6px;
    border: 1px solid #cfcfcf;
    transition: background 250ms;
    cursor: pointer;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    &:hover {
      background-color: ${(props) =>
        props.hoverColor ? props.hoverColor : "#d3d3d3"};
      color: #6c7377;
    }
    & span {
      font-weight: 600;
      text-transform: uppercase;
      margin: 0px 5px;
      @media (max-width: ${theme.screens.tablet}) {
        display: none;
      }
    }
  `;

  const EditButtonIcon = styled.img`
    width: 18px;
  `;

  return (
    <EditButtonEl
      onClick={onClick}
      data-tag="allowRowEvents"
      hoverColor={hoverColor}
      css={addlCss}
    >
      {icon && <EditButtonIcon src={icon} />}
      {!alwaysVisibleLabel && children && <span>{children}</span>}
      {alwaysVisibleLabel && children && <>{children}</>}
    </EditButtonEl>
  );
}

export default EditButton;
