/** @jsxImportSource @emotion/react */

import React, { useContext } from "react";
import { Context } from "../../App.js";
import DataTable from "react-data-table-component";
import EditIcon from "../../icons/edit.svg";
import TrashIcon from "../../icons/trash.svg";
import PlusIcon from "../../icons/plus.svg";
import Pagination from "./TablePagination";
import EditButton from "./EditButton";
import Status from "./Status";
import Button from "./Button";
import { css, useTheme } from "@emotion/react";
import customTableStyles from "./tableStyles.json";

const SiteInfo = css`
  margin: 0px;
  font-size: 13px;
  line-height: 18px;
`;

const SiteLocation = css`
  color: #6e777c;
  font-weight: 500;
`;

const SiteTablePagination = function (props) {
  const [state, dispatch] = useContext(Context);

  const handleAdd = () => {
    dispatch({ type: "ADD_SITE" });
  };

  return (
    <div
      css={css`
        display: flex;
        align-items: center;
        width: 100%;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
      `}
    >
      <Pagination {...props}></Pagination>
      <Button
        onClick={handleAdd}
        addlCss={css`
          margin-left: auto;
        `}
      >
        <img src={PlusIcon} />
        Add New Site
      </Button>
    </div>
  );
};

export default function ({ sites }) {
  const [state, dispatch] = useContext(Context);
  const theme = useTheme();

  const siteTableStyleOverrides = css`
    & .rdt_TableRow > .rdt_TableCell:last-child,
    & .rdt_TableCol:last-child {
      padding-right: 0px;
    }
    & .rdt_TableRow > .rdt_TableCell:first-child,
    & .rdt_TableCol:first-child {
      padding-left: 0px;
    }
    & .rdt_TableRow > .rdt_TableCell:nth-child(3),
    & .rdt_TableCol:nth-child(3) {
      flex-shrink: 1;
      flex-grow: 0;
    }
    @media (min-width: ${theme.screens.tablet}) {
      & .rdt_TableRow > .rdt_TableCell:nth-child(3),
      & .rdt_TableCol:nth-child(3) {
        flex-basis: 100px;
      }
    }
    @media (max-width: ${theme.screens.mobile}) {
      & .rdt_TableRow > .rdt_TableCell:nth-child(3),
      & .rdt_TableCol:nth-child(3) {
        flex-basis: 20px;
      }
    }
  `;

  const EditCell = (site) => {
    const [state, dispatch] = useContext(Context);

    const handleRemove = () => {
      var confirm = window.confirm(
        "Are you sure you want to delete this site? There are no take-backs."
      );
      if (confirm) {
        dispatch({ type: "REMOVE_SITE", payload: site });
      }
    };

    const handleEdit = () => {
      dispatch({ type: "SET_ACTIVE_SITE", payload: site });
    };

    return (
      <>
        <EditButton onClick={handleEdit} icon={EditIcon} />
        <EditButton
          onClick={handleRemove}
          hoverColor="rgba(245, 60, 63, 0.2)"
          icon={TrashIcon}
        />
      </>
    );
  };

  const siteManager = (site) => {
    let currentSiteManager = site.FormMetaData.SiteManager || "";

    const changeSiteManager = (e) => {
      site.FormMetaData.SiteManager = e.target.value;
      dispatch({ type: "CREATE_OR_UPDATE_SITE", payload: site });
    };

    return (
      <select
        data-tag="allowRowEvents"
        onChange={changeSiteManager}
        defaultValue={currentSiteManager}
      >
        <option value="">Assign Site Manager</option>
        {state.site_managers.map((manager, index) => (
          <React.Fragment key={index}>
            {manager.Email && (
              <>
                {manager.Name && (
                  <option value={manager.Email}>{manager.Name}</option>
                )}
                {!manager.Name && (
                  <option value={manager.Email}>{manager.Email}</option>
                )}
              </>
            )}
          </React.Fragment>
        ))}
      </select>
    );
  };

  const columns = [
    {
      name: "Site Name",
      selector: "Name",
      sortable: true,
      grow: 2,
      style: {
        paddingLeft: "0px",
      },
      cell: function (row) {
        let Name = row.Name ? row.Name : "";

        row.Address = row.Address ? row.Address : {};

        let City = row.Address.City ? row.Address.City : "";
        let State = row.Address.State ? row.Address.State : "";
        let Country = row.Address.Country ? row.Address.Country : "";
        let PostalCode = row.Address.PostalCode ? row.Address.PostalCode : "";
        let StreetNumber = row.Address.StreetNumber
          ? row.Address.StreetNumber
          : "";

        return (
          <div data-tag="allowRowEvents">
            <h5 css={[SiteInfo]}>{Name}</h5>
            <h6
              css={[SiteInfo, SiteLocation]}
              dangerouslySetInnerHTML={{
                __html:
                  StreetNumber +
                  "<br>" +
                  City +
                  ", " +
                  State +
                  " " +
                  PostalCode +
                  "<br>" +
                  Country,
              }}
            ></h6>
          </div>
        );
      },
    },
    {
      name: "Site Manager",
      selector: "siteManager",
      cell: siteManager,
      sortable: true,
      grow: 2,
    },
    {
      name: "Status",
      selector: "status",
      sortable: true,
      grow: 1,
      cell: (row) => (
        <>
          {row.Status == "Under Review" && (
            <Status color="#84488E" status={row.Status}>
              <span>{row.Status}</span>
            </Status>
          )}
          {row.Status == "Initialized" && (
            <Status color="#ffab40" status={row.Status}>
              <span>{row.Status}</span>
            </Status>
          )}
          {row.Status == "Complete" && (
            <Status color="#70c88b" status={row.Status}>
              <span>{row.Status}</span>
            </Status>
          )}
        </>
      ),
    },
    {
      name: "",
      sortable: false,
      noTableHead: true,
      right: true,
      grow: 1,
      cell: EditCell,
      conditionalCellStyles: [
        {
          when: () => {
            return true;
          },
          style: {
            paddingRight: "0px",
          },
        },
      ],
    },
  ];

  return (
    <DataTable
      columns={columns}
      data={sites}
      defaultSortField="Name"
      customStyles={customTableStyles}
      pagination
      paginationComponent={SiteTablePagination}
      noHeader={true}
      className="siteTable"
      css={siteTableStyleOverrides}
    />
  );
}
