/** @jsxImportSource @emotion/react */

import React, { useContext, useState, useMemo, useEffect, useRef } from "react";
import styled from "@emotion/styled";
import PopoverHeader from "../elements/PopoverHeader";
import PopoverForm from "../elements/Form";
import Status from "../elements/Status";
import Button from "../elements/Button";
import _ from "lodash";
import { useForm } from "react-hook-form";
import { css, useTheme } from "@emotion/react";
import { Context } from "../../App.js";

function SiteEditor() {
  const theme = useTheme();
  const [state, dispatch] = useContext(Context);
  const [formData, setFormData] = useState(null);
  const [mobileTabState, setMobileTabState] = useState("Site Details");
  const [initialized, setInitialized] = useState(false);
  const { register, handleSubmit } = useForm();

  useMemo(() => {
    if (!initialized) {
      setInitialFormValues();
    }
  }, [state.activeSite]);

  function setInitialFormValues() {
    var activeSite = state.activeSite;
    var formData = JSON.parse(JSON.stringify(state.form_data));

    setInitialized(true);

    //Map the site data to form data
    for (const [key, value] of Object.entries(state.form_mappings)) {
      var splitKey = key.split(".");
      var siteValue = {};

      //Get the value
      splitKey.forEach(function (formValue) {
        siteValue = siteValue[formValue]
          ? siteValue[formValue]
          : activeSite[formValue] || {};
      });

      //Find the field in the form data and set the value
      formData.items.forEach(function (formItem) {
        if (formItem.title == value.en && siteValue.length) {
          formItem.value = siteValue;
        }
      });
    }

    //Set meta fields
    if (state.activeSite.FormMetaData) {
      for (const [siteMetaKey, siteMetaValue] of Object.entries(
        state.activeSite.FormMetaData
      )) {
        formData.items.forEach(function (formItem) {
          if (formItem.title == siteMetaKey) {
            formItem.value = siteMetaValue;
          }
        });
      }
    }

    setFormData(formData);
  }

  function setInternalValuesFromForm(data) {
    var activeSiteCopy = JSON.parse(JSON.stringify(state.activeSite));
    activeSiteCopy.FormMetaData = activeSiteCopy.FormMetaData || {};

    for (const [dataKey, dataValue] of Object.entries(data)) {
      var foundInMap = false;
      for (const [mapKey, mapValue] of Object.entries(state.form_mappings)) {
        if (dataKey == mapValue.en) {
          _.set(activeSiteCopy, mapKey, dataValue);
          foundInMap = true;
        }
      }
      if (!foundInMap) {
        activeSiteCopy["meta"][dataKey] = dataValue;
      }
    }

    //next time we re-render, re-do the field mapping stuff
    setInitialized(false);

    dispatch({ type: "SET_ACTIVE_SITE", payload: activeSiteCopy });
    dispatch({ type: "CREATE_OR_UPDATE_SITE", payload: activeSiteCopy });

    alert("Saved!");
  }

  const onSubmit = (data) => setInternalValuesFromForm(data);

  const close = () => {
    setInitialized(false);
    setFormData(null);
    dispatch({ type: "SET_ACTIVE_SITE", payload: false });
  };

  const Buttons = (
    <>
      <Button
        onClick={close}
        addlCss={css`
          background: #ededed;
          color: #868f94;
          flex-basis: 135px;
          margin-right: 5px;
        `}
      >
        Close
      </Button>
      <Button
        type="submit"
        addlCss={css`
          background: #70c88b;
          color: #fff;
          flex-basis: 135px;
        `}
      >
        Save
      </Button>
    </>
  );

  const LoadingForm = styled.div`
    width: 100%;
    background: #fff;
    padding: 30px;
    margin: auto;
    font-weight: 600;
  `;

  const SiteManagerSelector = styled.div`
    display: flex;
    flex-wrap: wrap;
    height: calc(100% - 50px);
    overflow-y: scroll;
    overflow-x: hidden;
  `;

  const SiteManagerSelection = styled.div`
    display: flex;
    width: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 10px 0px;
    & input {
      margin-left: 0px;
      margin-right: 10px;
      padding: 0px;
    }
    & label {
      width: 100%;
      h5,
      h6 {
        font-size: 13px;
        font-weight: 500;
        line-height: 18px;
        margin: 0px;
      }
      h5 {
        color: #0d1e29;
      }
      h6 {
        color: #6e777c;
      }
    }
    &:last-of-type {
      border-bottom: 0px;
    }
  `;

  const SidebarHeader = styled.h5`
    width: 100%;
    margin-top: 0px;
    margin-bottom: 20px;
    color: #0d1e29;
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
  `;

  const PopoverTabs = styled.div`
    width: 100%;
    display: flex;
    cursor: pointer;
    border-bottom: 1px solid #e0e0e0;
    @media (min-width: ${theme.screens.tablet}) {
      display: none;
    }
  `;

  const PopoverTab = styled.div`
    width: 100%;
    font-size: 13px;
    line-height: 18px;
    text-transform: uppercase;
    padding: 9px 30px;
    background-color: ${(props) => (props.active ? "#FFF" : "#DDD")};
    color: ${(props) => (props.active ? "#0d1e29" : "#6e777c")};
    font-weight: 600;
    text-align: center;
    &:hover {
      color: #0d1e29;
    }
  `;

  const PopoverIntro = styled.p`
    padding: 30px;
    margin: 0px;
    font-size: 13px;
    font-weight: 500;
    line-height: 18px;
    color: #6e777c;
    @media (max-width: ${theme.screens.tablet}) {
      padding: 20px 20px 0px 20px;
    }
  `;

  const PopoverFormHeading = styled.div`
    width: 100%;
    display: flex;
    @media (max-width: ${theme.screens.tablet}) {
      flex-wrap: wrap;
    }
  `;

  const PopoverContent = styled.div`
    display: flex;
    min-height: calc(70vh - 60px);
    max-height: 500px;
  `;

  const PopoverStatus = styled.div`
    padding: 30px;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: flex-end;
    @media (max-width: ${theme.screens.tablet}) {
      padding: 0px 0px 0px 20px;
    }
    & h6 {
      text-transform: uppercase;
      margin-right: 10px;
      font-weight: 500;
      font-size: 12px;
      margin-bottom: 0px;
    }
  `;

  const PopoverMain = styled.div`
    width: 66.66%;
    height: auto;
    background: #fff;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: column;
    @media (max-width: ${theme.screens.tablet}) {
      width: 100%;
      display: ${(props) =>
        props.tabState == "Site Details" ? "flex" : "none"};
    }
  `;

  const PopoverSidebar = styled.div`
    width: 33.33%;
    padding: 30px;
    background: #e8e8e8;
    @media (max-width: ${theme.screens.tablet}) {
      width: 100%;
      background: #fff;
      padding: 15px;
      flex-wrap: wrap;
      display: ${(props) =>
        props.tabState == "Site Managers" ? "flex" : "none"};
    }
  `;

  const Popover = styled.div`
    width: 100%;
    backgroundcolor: #fff;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    margin: auto;
    z-index: 110;
    @media (min-width: ${theme.screens.tablet}) {
      max-width: calc(728px);
    }
    @media (min-width: ${theme.screens.desktop}) {
      max-width: calc(960px);
    }
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      z-index: 0;
      position: fixed;
      left: 0px;
      top: 0px;
      background: rgba(0, 0, 0, 0.5);
    }
  `;

  const PopoverInner = styled.div`
    width: 100%;
    position: relative;
    z-index: 5;
  `;

  return (
    <Popover>
      <PopoverInner>
        <form onSubmit={handleSubmit(onSubmit)}>
          {formData && (
            <>
              {state.activeSite && (
                <PopoverHeader title="Edit site" buttons={Buttons} />
              )}
              {!state.activeSite && (
                <PopoverHeader title="Add site" buttons={Buttons} />
              )}
              <PopoverTabs>
                <PopoverTab
                  active={mobileTabState == "Site Details" ? true : false}
                  onClick={() => {
                    setMobileTabState("Site Details");
                  }}
                >
                  Site Details
                </PopoverTab>
                <PopoverTab
                  active={mobileTabState == "Site Managers" ? true : false}
                  onClick={() => {
                    setMobileTabState("Site Managers");
                  }}
                >
                  Site Managers
                </PopoverTab>
              </PopoverTabs>
              <PopoverContent>
                <PopoverMain tabState={mobileTabState}>
                  <PopoverFormHeading>
                    <PopoverIntro>{formData.metadata.description}</PopoverIntro>
                    <PopoverStatus>
                      <h6>Status</h6>
                      {state.activeSite.Status == "Under Review" && (
                        <Status
                          color="#84488E"
                          status={state.activeSite.Status}
                        >
                          {state.activeSite.Status}
                        </Status>
                      )}
                      {state.activeSite.Status == "Initialized" && (
                        <Status
                          color="#ffab40"
                          status={state.activeSite.Status}
                        >
                          {state.activeSite.Status}
                        </Status>
                      )}
                      {state.activeSite.Status == "Complete" && (
                        <Status
                          color="#70c88b"
                          status={state.activeSite.Status}
                        >
                          {state.activeSite.Status}
                        </Status>
                      )}
                    </PopoverStatus>
                  </PopoverFormHeading>
                  <PopoverForm
                    site={state.activeSite}
                    registerRef={register}
                    formData={formData}
                  />
                </PopoverMain>
                <PopoverSidebar tabState={mobileTabState}>
                  <SidebarHeader>Assign Site Manager</SidebarHeader>
                  <SiteManagerSelector>
                    {state.site_managers.map((manager, index) => {
                      return (
                        <React.Fragment key={index}>
                          {manager.Email && (
                            <SiteManagerSelection>
                              <input
                                ref={register}
                                type="radio"
                                name="SiteManager"
                                defaultChecked={
                                  state.activeSite.FormMetaData.SiteManager ==
                                  manager.Email
                                    ? true
                                    : false
                                }
                                id={manager.Email}
                                value={manager.Email}
                              ></input>
                              <label htmlFor={manager.Email}>
                                <h5>{manager.Name}</h5>
                                <h6>{manager.Email}</h6>
                              </label>
                            </SiteManagerSelection>
                          )}
                        </React.Fragment>
                      );
                    })}
                  </SiteManagerSelector>
                </PopoverSidebar>
              </PopoverContent>
            </>
          )}
        </form>
        {!formData && <LoadingForm>Loading Form...</LoadingForm>}
      </PopoverInner>
    </Popover>
  );
}

export default SiteEditor;
