import React, { useContext, useMemo, useState } from "react";
import { Context, fb } from "../../App.js";
import Login from "./Login";

const Authentication = React.memo(function ({ children }) {
  const [state, dispatch] = useContext(Context);
  const [hasBoundAuthCheck, setHasBoundAuthCheck] = useState(false);

  useMemo(() => {
    if (!hasBoundAuthCheck) {
      setHasBoundAuthCheck(true);
      fb.auth().onAuthStateChanged(function (user) {
        if (user) {
          dispatch({ type: "UPDATE_CURRENT_USER", payload: user });
          const userDetailsRef = fb
            .firestore()
            .collection("Users")
            .doc(user.email);
          userDetailsRef.get().then(function (doc) {
            const userDetails = doc.data();
            dispatch({ type: "SET_CURRENT_USER_META", payload: userDetails });

            fb.firestore()
              .collection("EdgedSites")
              .get()
              .then((queryFragments) => {
                dispatch({
                  type: "SET_SITE_QUERY_FRAGMENTS",
                  payload: queryFragments.docs,
                });
                fb.firestore()
                  .collection("EdgedSites")
                  .onSnapshot(function (querySnapshot) {
                    dispatch({
                      type: "SET_SITE_QUERY_FRAGMENTS",
                      payload: querySnapshot.docs,
                    });
                  });
              })
              .then(function () {
                const getCompaniesCb = (data) => {
                  let companiesArr = [];
                  if (userDetails.FeatureMap.AdminView && !data.empty) {
                    data.forEach((doc) => {
                      companiesArr.push(doc.data());
                    });
                    dispatch({ type: "SET_COMPANIES", payload: companiesArr });
                  } else if (!userDetails.FeatureMap.AdminView && data.exists) {
                    companiesArr.push(data.data());
                    dispatch({ type: "SET_COMPANIES", payload: companiesArr });
                  } else {
                    dispatch({ type: "SET_COMPANIES", payload: false });
                    alert(
                      "Error: No applicable companies found for this user."
                    );
                  }
                  if (state.current_company == null && companiesArr.length) {
                    dispatch({
                      type: "SET_CURRENT_COMPANY",
                      payload: companiesArr[0],
                    });
                  }
                };

                if (userDetails.FeatureMap.AdminView) {
                  fb.firestore()
                    .collection("Companies")
                    .get()
                    .then(getCompaniesCb);
                } else {
                  fb.firestore()
                    .collection("Companies")
                    .doc(userDetails.Company)
                    .get()
                    .then(getCompaniesCb);
                }
              });
          });
        } else {
          dispatch({ type: "UPDATE_CURRENT_USER", payload: null });
        }
      });
    }

    if (!state.form_data) {
      const apiUrl =
        "https://script.google.com/macros/s/AKfycbzVYXomXQt6VFhsyxfq3Rmuoyq6Aot2J1_7PcxGbjmqNY2gbZw/exec";
      fetch(apiUrl)
        .then((response) => response.json())
        .then((data) => {
          dispatch({ type: "SET_FORM_DATA", payload: data });
          let FormMappingsRef = fb
            .firestore()
            .collection("FormMappings")
            .doc("Labels");

          FormMappingsRef.get().then((doc) => {
            let data = doc.data();
            dispatch({
              type: "SET_FORM_MAPPINGS",
              payload: data,
            });
          });
        })
        .catch((error) => {
          alert("Error retrieving form fields.");
          console.log(error);
        });
    }
  }, []);

  const doLogin = (email, password) => {
    fb.auth()
      .signInWithEmailAndPassword(email, password)
      .catch((error) => {
        // var errorCode = error.code;
        var errorMessage = error.message;
        alert(errorMessage);
      });
  };

  if (
    state.authenticated == true &&
    state.current_user &&
    state.current_user_meta &&
    state.current_company &&
    state.sites &&
    state.form_data &&
    state.form_mappings
  ) {
    return <>{children}</>;
  } else if (state.authenticated == false) {
    return (
      <>
        <Login doLogin={doLogin} />
      </>
    );
  } else {
    return <h5 style={{ textAlign: "center" }}>Loading...</h5>;
  }
});

export default Authentication;
