/** @jsxImportSource @emotion/react */

import styled from "@emotion/styled";
import { css } from "@emotion/react";

const Button = styled.button`
  cursor: pointer;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
  font-weight: 600;
  line-height: 12px;
  text-transform: uppercase;
  border-color: #0d1e29;
  background-color: #0d1e29;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 28px;
  border-radius: 6px;
  padding-right: 5px;
  padding-left: 5px;
  & img {
    margin-right: 5px;
  }
`;

function ButtonComponent({ children, addlCss, onClick, className, type }) {
  return (
    <Button
      type={type == "submit" ? "submit" : ""}
      css={addlCss}
      className={className}
      onClick={onClick}
      data-tag="allowRowEvents"
    >
      {children}
    </Button>
  );
}

export default ButtonComponent;
