import "./App.css";
import React, { createContext, useReducer, useContext } from "react";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import "firebase/firebase-storage";
import Layout from "./components/Layout";
import Sites from "./components/sections/Sites";
import SiteEditor from "./components/sections/SiteEditor";
import SiteManagers from "./components/sections/SiteManagers";
import Authentication from "./components/elements/Authentication";
import siteManagerStubs from "./data/managerMocks.json";

const initialState = {
  sites: [],
  site_managers: [],
  activeSite: false,
  authenticated: null,
  current_user: null,
  current_user_meta: null,
  current_user_permissions: null,
  current_company: null,
  current_campaign: "",
  form_mappings: null,
  form_data: null,
};

const firebaseConfig = window.firebaseConfig || {
  apiKey: "AIzaSyBceL07r88FOThUvh5H1MjJsFRGM-relFs",
  authDomain: "endeavour-inf-map.firebaseapp.com",
  databaseURL: "https://endeavour-inf-map.firebaseio.com",
  projectId: "endeavour-inf-map",
  storageBucket: "endeavour-inf-map.appspot.com",
  messagingSenderId: "686780267914",
  appId: "1:686780267914:web:c265357dff7ab75f4fdc73",
  measurementId: "G-23T8FDMHTP",
};

const fb = window.firebase || firebase.initializeApp(firebaseConfig);

const Reducer = (state, action) => {
  switch (action.type) {
    case "SET_FORM_DATA":
      return {
        ...state,
        form_data: action.payload,
      };
    case "SET_FORM_MAPPINGS":
      console.log(action.payload);
      return {
        ...state,
        form_mappings: action.payload,
      };
    case "UPDATE_CURRENT_USER":
      let authenticated = null;
      if (action.payload) {
        authenticated = true;
      } else {
        authenticated = false;
      }
      return {
        ...state,
        current_user: action.payload,
        authenticated: authenticated,
      };
    case "SET_SITE_QUERY_FRAGMENTS":
      return {
        ...state,
        sites: action.payload,
      };
    case "SET_CURRENT_USER_META":
      return {
        ...state,
        current_user_meta: action.payload,
        current_user_permissions: action.payload.FeatureMap,
      };
    case "SET_ACTIVE_SITE":
      if (action.payload)
        action.payload.FormMetaData = action.payload.FormMetaData || {};
      return {
        ...state,
        activeSite: action.payload,
      };
    case "SET_CURRENT_COMPANY":
      const orderedCampaigns = Object.keys(action.payload.Campaigns)
        .sort()
        .reduce((obj, key) => {
          obj[key] = action.payload.Campaigns[key];
          return obj;
        }, {});
      return {
        ...state,
        current_company: action.payload,
        current_campaign: Object.values(orderedCampaigns)[0].Name,
        site_managers: [],
      };
    case "SET_SITE_MANAGERS":
      return {
        ...state,
        site_managers: action.payload,
      };
    case "SET_COMPANIES":
      return {
        ...state,
        companies: action.payload,
      };
    case "SET_CURRENT_CAMPAIGN":
      return {
        ...state,
        current_campaign: action.payload,
      };
    case "ADD_SITE":
      let campaigns = {};
      campaigns[state.current_campaign] = "Initialized";
      return {
        ...state,
        activeSite: {
          FormMetaData: {},
          Geometry: {},
          Status: "Initialized",
          Name: "",
          Address: {},
          Campaigns: campaigns,
          PropertyInfo: {},
        },
      };
    case "REMOVE_SITE":
      fb.firestore().collection("EdgedSites").doc(action.payload.ID).delete();
      return {
        ...state,
      };
    case "CREATE_OR_UPDATE_MANAGER":
      if (!action.payload.ID) {
        let docRef = fb.firestore().collection("Users").doc();
        action.payload.Uid = docRef.id;
        action.payload.Company = state.current_company.Name;
        fb.firestore()
          .collection("Users")
          .doc(action.payload.Email)
          .set(action.payload, { merge: true })
          .catch(function (e) {
            console.log(e);
          });
      } else {
        fb.firestore()
          .collection("Users")
          .doc(action.payload.Email)
          .set(action.payload, { merge: true })
          .catch(function (e) {
            console.log(e);
          });
      }
      return {
        ...state,
      };
    case "REMOVE_MANAGER":
      fb.firestore().collection("Users").doc(action.payload.Email).delete();
      return {
        ...state,
      };
    case "CREATE_OR_UPDATE_SITE":
      if (!action.payload.ID) {
        let docRef = fb.firestore().collection("EdgedSites").doc();
        action.payload.ID = docRef.id;
        fb.firestore()
          .collection("EdgedSites")
          .doc(action.payload.ID)
          .set(action.payload, { merge: true })
          .catch(function (e) {
            console.log(e);
          });
      } else {
        fb.firestore()
          .collection("EdgedSites")
          .doc(action.payload.ID)
          .set(action.payload, { merge: true })
          .catch(function (e) {
            console.log(e);
          });
      }

      return {
        ...state,
      };
    default:
      return state;
  }
};

const Context = createContext(initialState);

const Store = ({ children }) => {
  const [state, dispatch] = useReducer(Reducer, initialState);
  return (
    <Context.Provider value={[state, dispatch]}>{children}</Context.Provider>
  );
};

function Editor() {
  const [state, dispatch] = useContext(Context);
  return (
    <>
      {state.activeSite && <SiteEditor />}
      <Sites />
      <SiteManagers />
    </>
  );
}

function App() {
  return (
    <Store>
      <Layout>
        <Authentication>
          <Editor />
        </Authentication>
      </Layout>
    </Store>
  );
}

export { Context, fb };
export default App;
