/** @jsxImportSource @emotion/react */

import React, { useCallback, useState, useEffect } from "react";
import styled from "@emotion/styled";
import { css } from "@emotion/react";

const getNumberOfPages = (rowCount, rowsPerPage) =>
  Math.ceil(rowCount / rowsPerPage);

const PaginationButtonWrapper = styled.div`
  display: flex;
  padding: 11px 0px;
  align-items: center;
`;

const PaginationLink = styled.button`
  display: flex;
  appearance: none;
  align-items: center;
  background: none;
  border: none;
  color: #868f94;
  font-size: 11px;
  font-weight: 500;
  line-height: 12px;
  cursor: pointer;
  &:disabled {
    opacity: 0.5;
  }
`;

const PaginationButton = styled.button`
  width: 24px;
  height: 24px;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  margin-right: 5px;
  border: 1px solid #cac8bf;
  border-radius: 50%;
  color: ${(props) => (props.selected ? "#0d1e29" : "#868f94")};
  background: ${(props) => (props.selected ? "#cac8bf" : "none")};
  font-size: 11px;
  font-weight: 600;
  text-align: center;
  &:hover {
    background: #d1d1d1;
  }
`;

const dividerStyle = css`
  margin: 0px 10px;
  font-size: 11px;
  line-height: 12px;
  font-weight: 600;
  color: #868f94;
`;

const prevNextStyle = css`
  margin: 0px 20px;
  display: flex;
`;

function TablePagination({
  rowsPerPage,
  rowCount,
  onChangePage,
  onChangeRowsPerPage,
  currentPage,
}) {
  const [count, setCount] = useState([]);
  const handlePrevious = useCallback(() => onChangePage(currentPage - 1), [
    currentPage,
    onChangePage,
  ]);
  const handleNext = useCallback(() => onChangePage(currentPage + 1), [
    currentPage,
    onChangePage,
  ]);
  const handleFirst = useCallback(() => onChangePage(1), [onChangePage]);
  const handleLast = useCallback(
    () => onChangePage(getNumberOfPages(rowCount, rowsPerPage)),
    [onChangePage, rowCount, rowsPerPage]
  );
  const handleRowsPerPage = useCallback(
    ({ target }) => onChangeRowsPerPage(Number(target.value), currentPage),
    [currentPage, onChangeRowsPerPage]
  );
  const numPages = getNumberOfPages(rowCount, rowsPerPage);
  const disabledLesser = currentPage === 1;
  const disabledGreater = currentPage === numPages;
  let pageCount = numPages;
  let pageArray = [];

  if (count.length !== numPages) {
    for (pageCount; pageCount >= 1; pageCount--) {
      pageArray.push(pageCount);
    }
    pageArray = pageArray.reverse();
    setCount(pageArray);
  }

  return (
    <PaginationButtonWrapper>
      {count.map((num) =>
        num == currentPage ? (
          <PaginationButton key={num} selected>
            <span>{num}</span>
          </PaginationButton>
        ) : (
          <PaginationButton key={num}>
            <span>{num}</span>
          </PaginationButton>
        )
      )}
      <div css={prevNextStyle}>
        <PaginationLink onClick={handlePrevious} disabled={disabledLesser}>
          ❮ PREV
        </PaginationLink>
        <span css={dividerStyle}>|</span>
        <PaginationLink onClick={handleNext} disabled={disabledGreater}>
          NEXT ❯
        </PaginationLink>
      </div>
    </PaginationButtonWrapper>
  );
}

export default TablePagination;
