/** @jsxImportSource @emotion/react */

import React, { useState, useEffect } from "react";
import styled from "@emotion/styled";
import { css, jsx } from "@emotion/react";
import chevronDown from "../../icons/chevron-down.svg";

const chevronStyle = css`
  width: 22px;
  margin-left: 10px;
`;

const Eyebrow = styled.h5`
  font-size: 13px;
`;

const HeaderText = styled.h2`
  font-family: adobe-caslon-pro, sans-serif;
  line-height: 100%;
  font-weight: 400;
  margin: 0px;
  display: inline-block;
  font-size: ${(props) => (props.dropdown ? "40px" : "32px")};
  cursor: ${(props) => (props.dropdown ? "pointer" : "normal")};
`;

const Dropdown = styled.nav`
  border: 1px solid rgba(0, 0, 0, 0.1);
  min-width: 500px;
  position: absolute;
  z-index: 100;
  background: #fff;
`;

const DropdownItem = styled.div`
  color: #000;
  padding: 15px 20px 8px 20px;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  &:hover {
    background: rgba(202, 200, 191, 0.22);
  }
  &:last-child {
    border-bottom: none;
  }
`;

function Header({
  eyebrow = "",
  text = "—",
  dropdownSelections = [],
  dropdownSelection = false,
  onSelect = () => {},
}) {
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (open) {
      document.addEventListener("click", function hideDropdownEvent(event) {
        setOpen(false);
        document.removeEventListener("click", hideDropdownEvent);
      });
    }
  });

  const onHeaderClick = () => {
    open ? setOpen(false) : setOpen(true);
  };

  return (
    <>
      {eyebrow.length > 0 && <Eyebrow>{eyebrow}</Eyebrow>}
      {!dropdownSelection && <HeaderText>{text}</HeaderText>}
      {dropdownSelection && (
        <HeaderText dropdown onClick={onHeaderClick}>
          {dropdownSelection.label}
          <img css={chevronStyle} src={chevronDown} />
          {open && (
            <Dropdown>
              {dropdownSelections.map((selection, index) => {
                return (
                  <DropdownItem
                    key={index}
                    onClick={() => {
                      onSelect(selection);
                    }}
                  >
                    {selection.label}
                  </DropdownItem>
                );
              })}
            </Dropdown>
          )}
        </HeaderText>
      )}
    </>
  );
}

export default Header;
