import SiteTable from "../elements/SiteTable";
import Header from "./Header";
import ContentWrapper from "../elements/ContentWrapper";
import styled from "@emotion/styled";
import React, { useContext, useState, useRef, useMemo } from "react";
import { Context } from "../../App.js";

const SitesSection = styled.section`
  margin-bottom: 40px;
`;

function Sites() {
  const [state, dispatch] = useContext(Context);
  const [currentCompanyDropdownItem, setCurrentCompanyDropdownItem] = useState(
    false
  );
  const [campaignDropdownItems, setCampaignDropdownItems] = useState([]);
  const [companyDropdownItems, setCompanyDropdownItems] = useState([]);
  const [sites, setSites] = useState([]);
  const builtDropdowns = useRef(false);

  let isAdmin = false;

  if (
    state.current_user_permissions &&
    state.current_user_permissions.AdminView
  ) {
    isAdmin = true;
  }

  useMemo(() => {
    if (state.current_campaign.length && state.sites.length) {
      let filteredSites = [];
      state.sites.forEach(function (docRef) {
        if (docRef.exists) {
          const data = docRef.data();
          if (data.Campaigns && data.Campaigns[state.current_campaign]) {
            data.FormMetaData = data.FormMetaData || { SiteManager: {} };
            filteredSites.push(data);
          }
        }
        setSites(filteredSites);
      });
    }
  }, [state.current_campaign, state.sites]);

  if (!builtDropdowns.current) {
    if (state.current_company && state.companies) {
      builtDropdowns.current = true;
      let dropdownItems = [];

      state.companies.forEach((company) => {
        dropdownItems.push({ label: company.Name, value: company });
      });

      setCompanyDropdownItems(dropdownItems);
    }
  }

  useMemo(() => {
    companyDropdownItems.forEach((company_dd) => {
      if (company_dd.value.Name == state.current_company.Name) {
        setCurrentCompanyDropdownItem(company_dd);
      }
    });

    const orderedCampaigns = Object.keys(state.current_company.Campaigns)
      .sort()
      .reduce((obj, key) => {
        obj[key] = state.current_company.Campaigns[key];
        return obj;
      }, {});

    setCampaignDropdownItems(orderedCampaigns);
  }, [companyDropdownItems, state.current_company]);

  const changeCompany = (dropdownItem) => {
    dispatch({ type: "SET_CURRENT_COMPANY", payload: dropdownItem.value });
  };

  const changeCampaign = (e) => {
    dispatch({ type: "SET_CURRENT_CAMPAIGN", payload: e.target.value });
  };

  return (
    <SitesSection>
      <ContentWrapper>
        {isAdmin && (
          <Header
            onSelect={changeCompany}
            dropdownSelection={currentCompanyDropdownItem}
            dropdownSelections={companyDropdownItems}
            eyebrow="ADMIN"
          />
        )}
        {!isAdmin && (
          <Header
            onSelect={changeCompany}
            text={
              currentCompanyDropdownItem
                ? currentCompanyDropdownItem.label
                : "—"
            }
            eyebrow="ADMIN"
          />
        )}
        {state.current_company &&
          state.current_campaign &&
          campaignDropdownItems && (
            <select
              defaultValue={state.current_campaign.Name}
              onChange={changeCampaign}
              style={{ position: "relative", zIndex: 99 }}
            >
              {Object.values(campaignDropdownItems).map((campaign, key) => (
                <option key={key} value={campaign.Name}>
                  {campaign.Name}
                </option>
              ))}
            </select>
          )}
        {state.sites.length && <SiteTable sites={sites} />}
        {!state.sites.length && <p>No sites available.</p>}
      </ContentWrapper>
    </SitesSection>
  );
}

export default Sites;
