import React, { useEffect, useState, useMemo, useContext } from "react";
import { Context } from "../../App.js";
import { fb } from "../../App.js";
import _ from "lodash";
import { useDropzone } from "react-dropzone";

const thumbsContainer = {
  display: "flex",
  flexDirection: "row",
  flexWrap: "wrap",
  marginTop: 16,
};

const thumb = {
  display: "inline-flex",
  borderRadius: 2,
  border: "1px solid #eaeaea",
  marginBottom: 8,
  marginRight: 8,
  width: 100,
  height: 100,
  padding: 4,
  boxSizing: "border-box",
};

const thumbInner = {
  display: "flex",
  minWidth: 0,
  overflow: "hidden",
};

const uploadingImg = {
  display: "block",
  width: "auto",
  height: "100%",
  opacity: 0.25,
};

const uploadedImg = {
  display: "block",
  width: "auto",
  height: "100%",
  opacity: 1,
};

const baseStyle = {
  flex: 1,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  padding: "20px",
  borderWidth: 2,
  borderRadius: 2,
  borderColor: "#eeeeee",
  borderStyle: "dashed",
  backgroundColor: "#fafafa",
  color: "#bdbdbd",
  outline: "none",
  transition: "border .24s ease-in-out",
};

const activeStyle = {
  borderColor: "#2196f3",
};

const acceptStyle = {
  borderColor: "#00e676",
};

const rejectStyle = {
  borderColor: "#ff1744",
};

function Uploader(props) {
  const [state] = useContext(Context);
  const [files, setFiles] = useState([]);
  let [urls, setUrls] = useState(props.urls || []);

  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject,
  } = useDropzone({
    accept: "image/*, .pdf",
    onDrop: (acceptedFiles) => {
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
    onDropAccepted: function (acceptedFiles) {
      var storageRef = fb.storage().ref();
      acceptedFiles.forEach((file) => {
        var acceptedFileRef = storageRef.child("siteImages/" + state.current_company.Name + "/" + file.name);
        acceptedFileRef.put(file).then((snapshot) => {
          snapshot.ref.getDownloadURL().then(function (downloadURL) {
            urls.push(downloadURL);
            setUrls(urls);
            setFiles(_.without(files, file));
          });
        });
      });
    },
  });

  const style = useMemo(
    () => ({
      ...baseStyle,
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {}),
    }),
    [isDragActive, isDragReject, isDragAccept]
  );

  const uploadingThumbs = files.map((file) => (
    <div style={thumb} key={file.name}>
      <div style={thumbInner}>
        <img src={file.preview} style={uploadingImg} />
      </div>
    </div>
  ));

  const uploadedThumbs = urls.map((url) => (
    <div style={thumb} key={url}>
      <div style={thumbInner}>
        <img src={url} style={uploadedImg} />
      </div>
    </div>
  ));

  useEffect(
    () => () => {
      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach((file) => URL.revokeObjectURL(file.preview));
    },
    [files]
  );

  return (
    <section className="container">
      <div {...getRootProps({ style })}>
        {urls.map((url, index) => (
          <input
            type="text"
            style={{ display: "none" }}
            name={props.name + "." + index}
            value={url}
            ref={props.registerRef}
          />
        ))}
        <input name={props.name} ref={props.registerRef} {...getInputProps()} />
        <p>Drag your images here, or click to select them.</p>
      </div>
      <aside style={thumbsContainer}>
        {uploadingThumbs}
        {uploadedThumbs}
      </aside>
    </section>
  );
}

/** Use onDropAccepted to upload to Firebase, get urlz, save it as text fields */

export default Uploader;
