import styled from "@emotion/styled";

const PopoverHeaderEl = styled.header`
  width: 100%;
  display: flex;
  background: #0d1e29;
  padding: 15px 15px 15px 20px;
  & h2 {
    width: 100%;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    margin: 0px;
    color: #fff;
  }
`;

const PopoverHeaderButtons = styled.div`
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin-left: auto;
`;

function PopoverHeader({ title, buttons }) {
  return (
    <PopoverHeaderEl>
      <h2>{title}</h2>
      <PopoverHeaderButtons>{buttons}</PopoverHeaderButtons>
    </PopoverHeaderEl>
  );
}

export default PopoverHeader;
