/** @jsxImportSource @emotion/react */

import styled from "@emotion/styled";
import Button from "./Button";
import { useRef } from "react";

const LoginFormWrap = styled.div`
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
`;

const LoginForm = styled.div`
  & form {
    display: flex;
    & input {
      width: 100%;
      margin: 0px 10px 0px 0px;
    }
    & button {
      flex-shrink: 0;
    }
  }
`;

const Login = function ({ doLogin }) {
  const email = useRef(null);
  const password = useRef(null);

  const onSubmit = (e) => {
    e.preventDefault();
    doLogin(email.current.value, password.current.value);
  };

  return (
    <LoginFormWrap>
      <LoginForm>
        <p>Please log in.</p>
        <form onSubmit={(e) => onSubmit(e)}>
          <input
            ref={email}
            placeholder="Email"
            type="email"
            id="email"
            name="email"
          ></input>
          <input
            ref={password}
            placeholder="Password"
            type="password"
            id="password"
            name="password"
          ></input>
          <Button type="submit">Log in</Button>
        </form>
      </LoginForm>
    </LoginFormWrap>
  );
};

export default Login;
