/** @jsxImportSource @emotion/react */

import DataTable from "react-data-table-component";
import EditIcon from "../../icons/edit.svg";
import TrashIcon from "../../icons/trash.svg";
import InviteIcon from "../../icons/invite.svg";
import EditButton from "./EditButton";
import React, { useContext, useState } from "react";
import { css, useTheme } from "@emotion/react";
import customTableStyles from "./tableStyles.json";
import { Context } from "../../App.js";

let managerTableStyles = JSON.parse(JSON.stringify(customTableStyles));
managerTableStyles.cells.style.paddingLeft = "10px";
managerTableStyles.cells.style.paddingRight = "10px";
managerTableStyles.headCells.style.paddingLeft = "10px";
managerTableStyles.headCells.style.paddingRight = "10px";

let newManager = {};

const inputStyles = css`
  margin: 0px !important;
`;

export default function ({ siteManagers }) {
  const [dispatch] = useContext(Context);
  const [editStatus, setEditStatus] = useState(false);
  const theme = useTheme();

  const managerTableStyleOverrides = css`
    @media (max-width: ${theme.screens.mobile}) {
      & .rdt_TableRow > .rdt_TableCell:last-child,
      & .rdt_TableCol:last-child {
        flex-grow: 2;
      }
    }
  `;

  const handleChange = (e) => {
    const field = e.target.attributes["data-field"].value;
    newManager = { ...newManager, [field]: e.target.value };
  };

  const handleSave = () => {
    if (newManager.Name) {
      dispatch({ type: "CREATE_OR_UPDATE_MANAGER", payload: newManager });
      setEditStatus(false);
    } else {
      alert("Site manager name is required.");
    }
  };

  const handleCancel = () => {
    setEditStatus(false);
  };

  const EditCell = (manager) => {
    const [inviteStatus, setInviteStatus] = useState(false);

    const handleEdit = () => {
      setEditStatus(manager.Email);
      newManager = manager;
    };

    const handleRemove = () => {
      var confirm = window.confirm(
        "Are you sure you want to delete this manager? There are no take-backs."
      );
      if (confirm) {
        dispatch({ type: "REMOVE_MANAGER", payload: manager });
      }
    };

    const handleInvite = () => {
      /**
       * @@ Evan -- invite code goes here!
       */
    };

    return (
      <>
        {editStatus !== manager.Email && (
          <>
            {inviteStatus && (
              <EditButton onClick={handleInvite} icon={InviteIcon}>
                <span>Invited!</span>
              </EditButton>
            )}
            {!inviteStatus && (
              <EditButton
                onClick={() => {
                  setInviteStatus(true);
                }}
                icon={InviteIcon}
              >
                Invite
              </EditButton>
            )}
            <EditButton onClick={handleEdit} icon={EditIcon} />
            <EditButton
              onClick={handleRemove}
              hoverColor="rgba(245, 60, 63, 0.2)"
              icon={TrashIcon}
            />
          </>
        )}
        {editStatus == manager.Email && (
          <>
            <EditButton alwaysVisibleLabel={true} onClick={handleCancel}>
              Cancel
            </EditButton>
            <EditButton
              alwaysVisibleLabel={true}
              onClick={handleSave}
              hoverColor={"rgba(112, 200, 139, 0.27)"}
              addlCss={css`
                background: rgba(112, 200, 139, 0.27);
                border: 1px solid rgba(112, 200, 139, 0.5);
              `}
            >
              Save
            </EditButton>
          </>
        )}
      </>
    );
  };

  const NameCell = (manager) => {
    return (
      <>
        {editStatus !== manager.Email && (
          <h5
            css={css`
              font-weight: 600;
              font-size: 13px;
              color: #0d1e29;
              margin: 0px;
            `}
          >
            {manager.Name ? manager.Name : "Not Set"}
          </h5>
        )}
        {editStatus == manager.Email && (
          <input
            css={inputStyles}
            onChange={handleChange}
            type="text"
            data-tag="allowRowEvents"
            defaultValue={manager.Name}
            placeholder="Name"
            data-field="Name"
          />
        )}
      </>
    );
  };

  const PhoneCell = (manager) => {
    return (
      <>
        {editStatus !== manager.Email && (
          <>{manager.Phone ? manager.Phone : "Not Set"}</>
        )}
        {editStatus == manager.Email && (
          <input
            css={inputStyles}
            onChange={handleChange}
            type="text"
            data-tag="allowRowEvents"
            defaultValue={manager.Phone}
            placeholder="Phone"
            data-field="Phone"
          />
        )}
      </>
    );
  };

  const EmailCell = (manager) => {
    return (
      <>
        {editStatus !== manager.Email && (
          <>{manager.Email ? manager.Email : "Not Set"}</>
        )}
        {editStatus == manager.Email && (
          <input
            css={inputStyles}
            onChange={handleChange}
            type="text"
            data-tag="allowRowEvents"
            defaultValue={manager.Email}
            placeholder="Email"
            data-field="Email"
          />
        )}
      </>
    );
  };

  const columns = [
    {
      name: "Name",
      selector: "name",
      sortable: true,
      grow: 1,
      cell: NameCell,
    },
    {
      name: "Phone",
      selector: "phone",
      sortable: true,
      grow: 1,
      cell: PhoneCell,
    },
    {
      name: "Email",
      selector: "email",
      sortable: true,
      grow: 1,
      cell: EmailCell,
    },
    {
      name: "",
      sortable: false,
      noTableHead: true,
      right: true,
      grow: 1,
      cell: EditCell,
    },
  ];

  return (
    <>
      <DataTable
        columns={columns}
        data={siteManagers}
        defaultSortField="name"
        customStyles={managerTableStyles}
        noHeader={true}
        css={managerTableStyleOverrides}
      />
    </>
  );
}
