import styled from "@emotion/styled";
import { css, useTheme } from "@emotion/react";
import React, { useContext, useState, useEffect, useMemo } from "react";
import ContentWrapper from "../elements/ContentWrapper";
import ManagerTable from "../elements/ManagerTable";
import Button from "../elements/Button";
import PlusIcon from "../../icons/plus.svg";
import Header from "./Header";
import { Context, fb } from "../../App.js";

const SiteManagersSection = styled.section`
  padding: 40px 0px;
  background: rgba(134, 143, 148, 0.2);
`;

function SiteManagers() {
  const [newSite, updateNewSite] = useState({ Name: "", Email: "", Phone: "" });
  const [state, dispatch] = useContext(Context);
  const [siteManagerRefs, setSiteManagerRefs] = useState();
  const theme = useTheme();

  const SiteManagerFooter = styled.div`
    width: 100%;
    background: #dedfdd;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    flex-wrap: wrap;
    padding: 8px;
    align-items: stretch;
    @media (min-width: ${theme.screens.mobile}) {
      flex-wrap: nowrap;
      padding: 0px;
    }
    & .colWrap {
      display: flex;
      align-items: center;
      padding: 5px 0px;
      max-width: 100%;
      min-width: 100px;
      flex-shrink: 0;
      flex-grow: 1;
      width: 100%;
      @media (min-width: ${theme.screens.mobile}) {
        width: auto;
        flex-basis: 0;
        padding: 10px;
      }
    }
    & .formInput {
      width: 100%;
      margin: 0px;
      @media (min-width: ${theme.screens.mobile}) {
        width: calc(100% - 20px);
      }
    }
  `;

  useMemo(() => {
    const fbSiteManagerRefs = fb
      .firestore()
      .collection("Users")
      .where("Company", "==", state.current_company.Name);
    fbSiteManagerRefs.onSnapshot(function (querySnapshot) {
      setSiteManagerRefs(querySnapshot);
    });
  }, [state.current_company]);

  useEffect(() => {
    if (siteManagerRefs) {
      let siteManagers = [];
      siteManagerRefs.forEach(function (docRef) {
        if (docRef.exists) {
          siteManagers.push(docRef.data());
        }
      });
      dispatch({ type: "SET_SITE_MANAGERS", payload: siteManagers });
    }
  }, [siteManagerRefs]);

  const handleChange = (e) => {
    const field = e.target.attributes["data-field"].value;
    updateNewSite({ ...newSite, [field]: e.target.value });
  };

  const handleAdd = (e) => {
    if (newSite.Name) {
      dispatch({ type: "CREATE_OR_UPDATE_MANAGER", payload: newSite });
      updateNewSite({ Name: "", Email: "", Phone: "" });
    } else {
      alert("Site manager name is required.");
    }
  };

  return (
    <SiteManagersSection>
      <ContentWrapper>
        <Header text="Site Managers" />
        <ManagerTable siteManagers={state.site_managers} />
        <SiteManagerFooter>
          <div className="colWrap">
            <input
              className="formInput"
              type="text"
              onChange={handleChange}
              value={newSite.Name}
              placeholder="Name"
              data-field="Name"
            />
          </div>
          <div className="colWrap">
            <input
              className="formInput"
              type="text"
              onChange={handleChange}
              value={newSite.Phone}
              placeholder="Phone"
              data-field="Phone"
            />
          </div>
          <div className="colWrap">
            <input
              className="formInput"
              type="email"
              onChange={handleChange}
              value={newSite.Email}
              placeholder="Email"
              data-field="Email"
            />
          </div>
          <div className="colWrap">
            <Button
              onClick={handleAdd}
              addlCss={css`
                flex-shrink: 0;
                margin-left: auto;
              `}
            >
              <img src={PlusIcon} />
              Add Site Manager
            </Button>
          </div>
        </SiteManagerFooter>
      </ContentWrapper>
    </SiteManagersSection>
  );
}

export default SiteManagers;
