/** @jsxImportSource @emotion/react */

import React from "react";
import FileUploader from "./FileUploader";
import styled from "@emotion/styled";
import { useTheme } from "@emotion/react";

function PopoverForm({ formData, registerRef }) {
  const theme = useTheme();

  const PopoverFormContainer = styled.div`
    width: 100%;
    margin: auto;
    padding: 30px;
    background: #f9f9f9;
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    overflow-y: scroll;
    overflow-x: hidden;
    @media (max-width: ${theme.screens.tablet}) {
      padding: 20px;
    }
  `;

  const FormItem = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 21px;
    & input,
    & textarea {
      width: 100%;
      margin: 0px;
    }
  `;

  const FormLabel = styled.label`
    width: 100%;
    margin-bottom: 10px;
    color: #0d1e29;
    font-size: 13px;
    font-weight: 600;
    text-transform: none;
    & span {
      color: red;
    }
  `;

  const CheckboxItem = styled.div`
    width: 100%;
    margin-bottom: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    & label {
      width: 100%;
      font-weight: normal;
      font-size: 12px;
      flex-shrink: 0;
    }
    & input[type="checkbox"] {
      flex-shrink: 0;
      flex-basis: 10px;
      margin-right: 10px;
    }
    &:last-of-type {
      margin-bottom: 0px;
    }
  `;

  return (
    <>
      <PopoverFormContainer>
        {formData.items.map((field, index) => {
          return (
            <FormItem key={index}>
              <FormLabel>
                {field.title}
                {field.isRequired && <span>&nbsp;*</span>}
              </FormLabel>
              {field.type == "TEXT" && (
                <input
                  id={field.title}
                  name={field.title}
                  ref={registerRef}
                  defaultValue={field.value}
                  required={field.isRequired ? field.isRequired : false}
                  type="text"
                />
              )}
              {field.type == "PARAGRAPH_TEXT" && (
                <textarea
                  defaultValue={field.value}
                  id={field.title}
                  name={field.title}
                  ref={registerRef}
                  required={field.isRequired ? field.isRequired : false}
                ></textarea>
              )}
              {field.type == "LIST" && field.choices && (
                <select
                  id={field.title}
                  defaultValue={field.value}
                  name={field.title}
                  ref={registerRef}
                  required={field.isRequired ? field.isRequired : false}
                >
                  <option key="blank" value="">
                    Select
                  </option>
                  {field.choices.map((choice, index) => {
                    return (
                      <option key={index} value={choice}>
                        {choice}
                      </option>
                    );
                  })}
                </select>
              )}
              {field.type == "CHECKBOX" && (
                <>
                  {field.choices.map((choice, index) => {
                    return (
                      <CheckboxItem key={index}>
                        <input
                          ref={registerRef}
                          type="checkbox"
                          id={choice}
                          defaultValue={choice}
                          name={field.title}
                          required={field.isRequired ? field.isRequired : false}
                        />
                        <label htmlFor={choice}>{choice}</label>
                      </CheckboxItem>
                    );
                  })}
                </>
              )}
              {field.type == "FILE_UPLOAD" && (
                /** Currently File Upload items can't be required due to a weird absence of isRequired() in the Google App Script API for that item type */
                <FileUploader
                  urls={field.value}
                  name={field.title}
                  registerRef={registerRef}
                  required={field.isRequired ? field.isRequired : false}
                />
              )}
            </FormItem>
          );
        })}
      </PopoverFormContainer>
    </>
  );
}

export default PopoverForm;
